<template>
<main class="two">
  <div class="content">
    <div class="btn primary back" @click="goto('/')">На главную</div>
    <div v-if="!this.$route.query.error" class="about-content">
      <h2 class="mt-05"><span class="mr-05">Спасибо</span><i class="material-icons red">favorite</i></h2>
      <p class="mt-05">Благодарю Вас за установку моего бота на сервер.<br>Для его активации просто напишите в любой канал <code>pc-init</code>.</p>
      <p>Ну и немного по пунктам:
        <ul>
          <li>Канал созданный ботом удалять <span class="red">нельзя</span></li>
          <li>Категорию созданную ботом удалять <span class="red">нельзя</span></li>
          <li>Переименовывать канал созданный ботом <span class="green">можно</span></li>
          <li>Модифицировать канал созданный ботом <span class="green">можно</span></li>
          <li>Переименовывать категорию созданную ботом <span class="green">можно</span></li>
        </ul>
      </p>
      <form class="about" method="POST" action="https://money.yandex.ru/quickpay/confirm.xml">
        <h3>Поддержать разраба</h3>
        <div class="flex-2 mt-1">
          <div class="form inline">
            <label for="sum" :class="{'active': sum, 'focus': active.sum}" class="input">Сумма</label>
            <input required id="sum" @focus="activate('sum')" @blur="deactivate('sum')" @change="test" label="Сумма" type="number" min="2" v-model="sum" name="sum" autocomplete="off">
          </div>
          <div class="form inline">
            <label for="comment" :class="{'active': comment, 'focus': active.comment}" class="input">Комментарий</label>
            <textarea id="comment" @focus="activate('comment')" @blur="deactivate('comment')" @change="test" label="Комментарий" name="comment" autocomplete="off" v-model="comment" rows="1"></textarea>
          </div>
        </div>
        <div class="radios" required>
          <p class="mt-05">Тип оплаты</p>
          <div class="form inline">
            <input checked required v-model="type" type="radio" name="paymentType" id="PC" class="md-radio">
            <label class="radio" for="PC">Я.Деньги</label>
          </div>
          <div class="form inline">
            <input required v-model="type" type="radio" name="paymentType" id="AC" class="md-radio">
            <label class="radio" for="AC">Карточка</label>
          </div>
          <div class="form inline">
            <input required v-model="type" type="radio" name="paymentType" id="MC" class="md-radio">
            <label class="radio" for="MC">С телефона</label>
          </div>
        </div>
        <button class="btn primary-f mt-05" type="submit">Готово</button>
        <div class="hidden">
          <input type="hidden" name="receiver" value="410017502127372">
          <input type="hidden" name="formcomment" value="Пожертвование разработчику RoomsBot'a">
          <input type="hidden" name="short-dest" value="Донат разрабу RoomsBot'a">
          <input type="hidden" name="quickpay-form" value="donate">
          <input type="hidden" name="targets" value="Пожертвование разработчику RoomsBot'a">
        </div>
      </form>
    </div>
    <div v-else class="about-content">
      <h2>Ошибка</h2>
      <p>По неизвестной никому причине произошла ошибка и бот добавиться не смог... Попробуете ещё разок? <a href="https://bit.ly/RoomBotInvite">Добавить бота</a></p>
    </div>
  </div>
</main>
</template>
<script>
import axios from 'axios';
export default {
  name: 'vue',
  data () {
    return {
      sum: "",
      type: "",
      comment: "",
      active: {
        sum: false,
        comment: false
      }
    }
  },
  methods: {
    goto(path) {
        this.$router.push(path)
    },
    test() {
      if (this.sum < 2) {
        this.sum = 2;
      }
    },
    activate(id) {
      this.active[id] = true;
    },
    deactivate (id) {
      this.active[id] = false;
    }
  }
}
</script>